import React, {useEffect} from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import {X as CloseIcon} from 'react-feather'
import SliderNav from 'react-slick'
import lpCover from 'images/lpCover.jpg'
import workCover from 'images/workCover.jpg'
import mapIndexed from 'helpers/mapIndexed'
import {range} from 'ramda'

const BGMusk = styled('div')`
  margin: 0 !important;
  position: fixed;
  top: 0;
  right: 0;
  bottom: ${({open}) => open ? 0 : '100vh'};
  left: 0;
  opacity: ${({open}) => open ? 1 : 0};
  z-index: ${({open}) => open ? 900 : -1000};
  transition: opacity .2s ease-in-out;
  background: #000000c2;
`
const ViewerWrap = styled('div')`
  margin: 0 !important;
  padding: 20px;
  position: fixed;
  top: 0;
  right: 0;
  bottom: ${({open}) => open ? 0 : '100vh'};
  left: 0;
  opacity: ${({open}) => open ? 1 : 0};
  z-index: ${({open}) => open ? 1000 : -1000};
  transition: opacity .2s ease-in-out;
  & > *{
    opacity: ${({open}) => open ? 1 : 0};
    z-index: ${({open}) => open ? 10000 : -1000};
    transition: opacity .2s ease-in-out;
  }
`
const CloseButton = styled('button')`
  overflow: hidden;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  border: 1px solid rgba(0,0,0,.1);
  color: ${({theme}) => theme.color.minor};
  background: inherit;
  transition: ${({theme}) => theme.transition.primary};
  top: 20px;
  right: 20px;
  z-index: 1000;
  &:before{
    content: " ";
    background: inherit; 
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    box-shadow: inset 0 0 0 3000px rgba(255,255,255,0.3);
    filter: blur(10px);
  }
  &:hover{
    color: ${({theme}) => theme.color.linkHover};
    transition: ${({theme}) => theme.transition.primary};
  }
`
const StyledSliderNav = styled(SliderNav)`
  position: fixed !important;
  bottom: 0;
  right: 0;
  left: 0;
  & .slick-track{
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-content: center;
  }
`
const NavWrap = styled('div')`
  width: 180px;
  height: 180px;
  padding: 0;
  display: flex !important;
  justify-content: center;
`
const NavInner = styled('div')`
  display: flex !important;
  justify-content: center;
  align-items: center;
  height: 170px;
  width: 170px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  border-radius: 4px;
  border: 1px solid rgba(0,0,0,.1);
  color: ${({theme}) => theme.color.minor};
  background: inherit;
  font-weight: 700;
  transition: ${({theme}) => theme.transition.primary};
  img{
    max-width: 100%;
    max-height: 100%;
  }
`
const ImageViewer = styled('div')`
  position: fixed;
  width: 100%;
  max-width: 88vw;
  max-height: calc(100vh - 190px);
  top: 70px;
  bottom: 220px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 4px;
  border: 1px solid rgba(0,0,0,.1);
  color: ${({theme}) => theme.color.minor};
  background: inherit;
  font-weight: 700;
  transition: ${({theme}) => theme.transition.primary};
  img{
    max-width: 100%;
    max-height: 100%;
  }
`

const WorksViewer = (props) => {
  const {
    open,
    setOpen
  } = props

  // useEffect
  useEffect(() => {
    if (open) {
      document.body.style.overflow = 'hidden'
    } else {
      document.body.style = null
    }
  }, [open])

  const windowWidth = window.screen.width

  // Nav settings
  const navSettings = {
    slidesToShow: Math.round(windowWidth / 250),
    slidesToScroll: 1,
    centerMode: true,
    arrows: false,
    // infinite: true,
    focusOnSelect: true
  }

  return (
    <>
      <BGMusk
        open={open}
      />
      <ViewerWrap
        open={open}
      >
        <CloseButton
          onClick={() => setOpen()}
        >
          <CloseIcon />
        </CloseButton>
        <ImageViewer>
          <img src={lpCover} alt={'Rou IMG'} />
        </ImageViewer>
        <StyledSliderNav {...navSettings}>
          {mapIndexed((item, index) => {
            return (
              <NavWrap key={index}>
                <NavInner>
                  <img src={workCover} alt={'Rou IMG'} />
                </NavInner>
              </NavWrap>
            )
          }, range(0, 3))}
        </StyledSliderNav>
      </ViewerWrap>
    </>
  )
}

WorksViewer.propTypes = {
  open: PropTypes.bool.isRequired,
  setOpen: PropTypes.func.isRequired
}

export default WorksViewer
