import React, {useState} from 'react'
import styled from 'styled-components'
import {WorkCard} from 'components/Cards'
import {mediaQueries} from 'constants/mediaQueries'
import {map, path, prop} from 'ramda'
import {appendParamsToUrl} from 'helpers/url'
import {useHistory} from 'react-router-dom'
import WorksViewer from 'components/WorksViewer'

const Wrap = styled('div')`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 0 30px;
  & > *:nth-child(even) {
    margin-top: 30px;
  }
  @media ${mediaQueries.tabletS}{
    grid-template-columns: 1fr;
    grid-gap: 30px 0;
    & > *:nth-child(even) {
      margin-top: 0;
    }
  }
  @media ${mediaQueries.mobileL}{
    grid-gap: 10px 0;
  }
`

const Works = (props) => {
  const {
    works
  } = props

  // useStates
  const [imageViewer, setImageViewer] = useState(true)

  // Redirects
  const history = useHistory()
  const goImageViewer = (id) => {
    history.push({
      pathname: path(['location', 'pathname'], history),
      search: appendParamsToUrl({imageViewer: id}, path(['location', 'search'], history))
    })
  }

  // Works list
  const worksList = map((item) => {
    const title = prop('title', item)
    const subtitle = prop('subtitle', item)
    const year = prop('year', item)
    const img = prop('img', item)
    const withDetail = prop('withDetail', item)
    const id = prop('id', item)

    return (
      <WorkCard
        title={title}
        subtitle={subtitle}
        year={year}
        img={img}
        withDetail={withDetail}
        key={id}
        onLink={() => {
          setImageViewer(true)
          goImageViewer(id)
        }}
      />
    )
  }, works)

  return (
    <Wrap>
      {worksList}
      <WorksViewer
        open={imageViewer}
        setOpen={setImageViewer}
      />
    </Wrap>
  )
}

export default Works
